@import './../../styles/index.scss';

.title {
  margin: 10px 0;
  font-size: $fs-quantity;

  @media screen and (max-width: 450px) {
    font-size: $fs-card-title;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;

}

.price {
  border-radius: 30px;
  padding: 5px 25px;
  background-color: $c-box;
  color: white;
  font-size: $fs-quantity;

  @media screen and (max-width: 450px) {
    font-size: $fs-card-title;
    padding: 5px 20px;
  }
}

.description {
  color: $c-box;
  width: 100%;
  max-width: 500px;

  @media screen and (min-width: 550px) and (max-width: 650px) {
    max-width: 300px;
  }

  @media screen and (max-width: 550px) {
    max-width: 200px;
    font-size: $fs-little;
  }
}

.actions {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 20px;

  @media screen and (max-width: 400px) {
    gap: 10px;
  }
}

.addButton {
  background-color: $c-header;
  border:none;
  color: $c-heading;
  padding: 7px 7px;
  // font-weight: $fw-bold;
  transition: $t-normal;
  cursor: pointer;

  @media screen and (max-width: 450px) {
    font-size: $fs-little;
  }


  &:hover {
    background-color: $c-box;
    border-color: $c-box;
    color: $c-main-bg;
  }
}