@import './../../styles/index.scss';

.header {
  width: 100%;
  height: 80px;
  padding: 0 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: $c-box;

  @media screen and (min-width: 450px) and (max-width: 600px) {
    padding: 30px;
  }

  @media screen and (max-width: 450px) {
    padding: 20px;
  }
}

.heading {
  color: $c-header;

  @media screen and (min-width: 450px) and (max-width: 600px) {
    font-size: $fs-header;
  }

  @media screen and (max-width: 450px) {
    font-size: $fs-quantity;
  }
}

.header ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
