@import './../../styles/index.scss';

.button {
  padding: 5px;
  background-color: transparent;
  border: 2px solid $c-bg;
  color: $c-bg;
  transition: $t-normal;
}

.button:hover,
.button:active {
  color: $c-main-bg;
  border-color: $c-main-bg;
}

.buttonText {
  margin: 0 10px;
}

.badge {
  background-color: $c-main-bg;
  border-radius: 30px;
  padding: 0.15rem 1.25rem;
  color: $c-box;
}