@import './../../styles/index.scss';

.item {
  margin: 20px 0;
  background-color: $c-header;
  padding: 20px;
  cursor: pointer;
  
}

.title {
  color: $c-main-bg;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.details {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.quantity {
  font-size: $fs-quantity;
  font-weight: $fw-bold;
}

.price {
  font-size: $fs-price;
  font-weight: $fw-bold;
  color: $c-main-bg;

  @media screen and (max-width: 450px) {
    font-size: $fs-card-title;
  }
}

.itemPrice {
  font-weight: $fw-regular;
  color: $c-bg;
  font-size: $fs-main;
  font-style: italic;

    @media screen and (max-width: 450px) {
    font-size: $fs-little;
  }
}

.actions {
  display: flex;
  justify-content: flex-end;
  margin: 10px 0;
}

.button {
  background-color: transparent;
  border: 1px solid $c-main-bg;
  margin-left: 0.5rem;
  padding: 2px 15px;
  color: $c-main-bg;
  transition: $t-normal;
}

.button:hover,
.button:active {
  background-color: $c-box;
  color: $c-main-bg;
}