// ? Fonts
// Font Families
$ff-main: 'Open Sans', sans-serif;

// Font sizes
$fs-little: 14px;
$fs-main: 16px;
$fs-card-title: 18px;
$fs-quantity: 22px;
$fs-price: 24px;
$fs-header: 26px;


// Font weights
$fw-regular: 400;
$fw-bold: 700;

// ? Colors
$c-main-bg: #fff;
$c-bg: #aec8c9;
$c-bg-two: #e6fdf3ac;
$c-header: #46696b;
$c-black: #000000;
$c-box: #204244;
$c-heading: #feffff;
$c-text: #394948;

// ? Transition
$t-normal: 0.35s cubic-bezier(0.4, 0, 0.2, 1);

